@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,300;1,700&display=swap');

$gr: #87C232;
$g: #939496;
$w: rgb(255, 255, 255);

@mixin res ($min : 480px, $max: 992px) {
  @media(min-width: $min) and (max-width: $max) {
      @content;
  }
}

html {
  font-size: 16px;
}

body {
  font-family: 'Roboto', sans-serif;
  background-color: #191919;
  p {
    font-size: 16px;
    color: $g;
  }
}

@keyframes example {
  from {
    transform: scale(1);
  }

  to {
    transform: scale(1.1);
  }
}

a {
  text-decoration: none;
}

header {
  border-bottom: 1px solid $g;
  button{
    background-color: transparent !important;
    border: none;
  }
  .show{
    top: 0%;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
   height: auto;
    background-color: #125775;
}
.position-relative{
  left: 15%;
  width: 100%;
  padding: 10px;

}
.form-control{
  border-radius: 0 !important;
}

.search{
  right: 0.9%;
  top: 13%;
  padding: 16.7px;
  background-color: $gr !important;
}
.hide{
  right: -5%;
  top: 3%;
  font-weight: 700;
  font-size: 2.5rem;
}

  .top {
    background-color: black !important;
    font-size: 14px;
    @include res() {
      display: none;
     }

    p {
      font-size: 14px;
    }

    .list-inline-item {
      padding: 0 15px;
      color: $g;

      a {
        color: $g;
      }
    }

    .center {
      border-left: 1px solid $g;
      border-right: 1px solid $g;
    }
  }

  a{
    color: $g !important;
    text-transform: uppercase !important;
    margin-left: 10px !important;
    margin-right: 10px !important;
    font-weight: 700;
    font-size: 16px;
    line-height: 2.5rem;

    &:hover {
      color: $w !important;
    }
  }

  .active {
    border-bottom: 4px solid $gr;
    color: $w !important;
  }

  .navbar-expand-lg {
    background-color: transparent !important;
    @include res() {
      background-color: #2B2B2B !important;
     }
  }
  .navbar-toggler{
    background-color: $gr;
  }

  .btn {
    color: $w !important;
    font-weight: 900;
    @include res() {
      display: none;
     }
  }

  .bg-primary {
    background-color: $gr !important;
    border-radius: 2px;
    padding: 12px 30px;
  }
}
.banner
{
  height: 4rem !important;
 background-color: red;
}
.carousel-item {
  height: 50% !important;
  .w-100 {
    filter: brightness(20%);
    animation-name: example;
    animation-duration: 5s;
    height: 100% !important;
  }

  .carousel-caption {
    position: absolute;
    right: 15%;
    left: 15%;
    top: 30%;
    bottom: 50%;
    @include res() {
      top: 28% !important;
     }

    p {
      padding: 0 16rem;
      font-size: 1.1rem;
      line-height: 2rem;
      @include res() {
        padding: 0;
        font-size: 0.9rem;
       }
    }

    h5 {
      font-size: 3rem !important;
      font-weight: 300;
      font-style: italic;
      @include res() {
        font-size: 1.4rem !important;
       }
    }

    h2 {
      font-style: italic;
      font-weight: 700;
      font-size: 3rem !important;
      @include res() {
        font-size: 1.4rem !important;
       }
    }
  }
}

.btn-primary {
  position: relative;
  text-transform: uppercase;
  overflow: hidden;
  border: none;
  &:hover {
    &::before {
      width: 100%;
      height: 100%;
      position: absolute;
      border-radius: 4px;
      content: "";
      background: $gr;
      top: 0;
      left: 0;
      z-index: -1;
    }
  }
}

.top {
  z-index: 1;
  &::before {
    width: 40px;
    height: 40px;
    position: absolute;
    border-radius: 50%;
    content: "";
    background: $gr;
    top: -60PX;
    left: 10px;
    z-index: -1;
    transition: 0.9s ease 0s;
  }
}
.btn-primary{
  background-color: #125875 !important;
}
.info{
  .row{
    .col-lg-3{
      border-radius: 5px !important;
    background-color: $w;
      &:hover{
        background-image: url(./assets/bg-img3.jpg);
        box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
        background-repeat: no-repeat;
        color: $w;
        transform: translateY(-15px);
        transition: .9s;
        i{
          color: yellow !important;
        }
        a{
          color: yellow !important;
        }
      }
    }
  }
  .text-success{
    color: $gr !important;
  }
  h4{
    font-size: 1.2rem;
    font-weight: 700;
  }
  h5{
    font-size: 1.05rem;
    font-weight: 700;
  }

}
.class{
  h2{
    font-size: 2.3rem;
    line-height: 3rem;
    font-weight: 400;
    color: $gr;
    font-style: italic;
    span{
      font-weight: 700;
    }
  }
  P{
    font-size: 1.05rem;
    line-height: 2rem;
    span{
      color: $gr;
      font-style: italic;
    }
  }
  .box{
    height: 30rem;
    display: inline-block;
    &:hover{
      h3, p{
        transform: translateY(-200px);
        transition: .9s;
      }
      img{
        filter: brightness(10%);
      }
      a{
        color: $gr;
      }
    }
  }.owl-item{
    right: 6.25%;
    @include res() {
     right: 6.2%;
    }
  }
  img{
    height: 100%;
    filter: brightness(50%);
    border-radius: 5px !important;
  }
  .owl-theme .owl-dots{
    display: none;
  }
  .position-absolute{
    bottom: -2%;
    right: 4%;
    left: 4%;
    padding: 30px 90px;
    color: $w !important;
    font-weight: 700;
  }
   .owl-prev{
    position: absolute;
    top: 40%;
    color: $w !important;
    left: -5% !important;
    font-size: 3rem !important;
    transition: 1s;
    &:hover{
      background-color: $gr !important;
    }
    span{
      padding: 0px 25px;
    }
   }
   .owl-next{
    position: absolute;
    top: 35%;
    right: -5%;
    color: $w !important;
    font-size: 3rem !important;
    transition: 1s;
    span{
      padding: 0px 25px;
    }
    &:hover{
      background-color: $gr !important;
    }
   }
}
.marquee-stroke{
  -webkit-text-stroke: 1px $gr;
  font-size: 4.5rem;
  color: #191919 !important;
  font-weight: 700;
  font-style: italic;
}

.instr{
  h2{
    font-size: 2.4rem;
    color: $gr;
    line-height: 3.5rem;
  }
  .ps-5{
    padding-left: 14rem !important;
    @include res() {
      padding-left: 0 !important;
     }
  }
  p{
    line-height: 2rem;
  }
  ul{
    li{
      list-style: none;
      font-style: italic;
      font-weight: 600;
      i{
        color: $gr;
      }
    }
  }
}
.instru{
  .owl-nav{
    display: none;
      }
     .owl-dots{
      display: none;
     }
     h3{
      font-size: 1.2rem !important;
      font-weight: 700;
     }
     P{
      font-size: 1rem !important;
      color: $gr;
     }
     .owl-item {
     padding: 0 10px !important;
     img{
      border-radius: 5px;
     }
     }
}
.member{
  h2{
    color: $gr;
    font-size: 2.7rem;
    font-weight: 300;
    font-style: italic;
    @include res() {
      font-size: 1.7rem;
     }
  }
  .px-5{
    padding: 0 20rem !important;
    @include res() {
      padding: 0 !important;
     }
    line-height: 2rem;
    span{
      color: $gr;
      font-style: italic;
    }
  }
  .grid{
    .text-success{
      color: $gr !important;
    }
    .bg-secondary{
      background-color: #1F1F1F !important;
      color: $w;
    }
   .col{
    &:hover{
      border: 2px solid $gr;
      color: red;
      .hidden{
        display: flex !important;
      }
      .show{
        display: none !important;
      }
    }
   }
  }
  .end{
    P{
      &:hover{
        color: $w !important;
      }
    }

   }
}
.goal{
  background: url(https://assets.website-files.com/5bf05334017c212448095927/60016869939b1c17a239f2f1_fitness-plus-gym.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  .bg{
    background-color: rgba(0, 0, 0, 0.667);
    .text-success{
      color: $gr !important;
    }
    i{
      font-size: 6.8rem;
      color: $gr;
    }
  }
  .form-control{
    border-radius: 0 !important;
    width: 25rem;
    padding-top: 1.3rem;
    padding-bottom: 1.3rem;
  }
  .position-absolute{
    right: 0;
    top: 0;
    padding: 1.35rem 1rem;
  }
  .btn-primary{
    background-color: $gr !important;
    color: $w;
  }
}
.equ{
  .col-lg-3{
   filter: grayscale(100%);
   &:hover{
    filter: grayscale(0);
   }
  }
}
.time{
  @include res() {
    display: none;
   }
  .container{
    background-color: #242424; 
  }
 
  .border-bottom{
    border-bottom: 1px solid rgba(128, 128, 128, 0.066) !important;
  }
  .bg-danger-subtle{
    background-color: #C27BA0 !important;
  }
}
.footer{
  li{
    p{
      border-bottom: 1px solid rgba(128, 128, 128, 0.174);
      &:hover{
        color: $w;
      }
    }
  }
  .con{
    .d-flex{
      border-bottom: 1px solid rgba(128, 128, 128, 0.144);
      &:hover{
        i{
          color: $w;
        }
      }
    }
  }
}
.ban{
  background: url(./assets/gym.avif);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  .bg{
    background-color: #191919c5 !important;
  }
  .box{
    padding: 15rem !important;
    padding-top: 19rem !important;
    @include res() {
      padding: 5rem !important;
     }
    h2{
      color: $gr;
      font-size: 4rem;
      font-weight: 400;
      @include res() {
        font-size: 2.5rem;
       }
    }
    p{
      font-size: 1.2rem;
    }

    .mx-5{
      margin: 0 17rem !important;
      border-radius: 10px;
    }
  }
}
.about{
  .text-success{
    color: $gr !important;
  }
  p{
    font-weight: 400;
    line-height: 2.5rem;
    text-align: justify;
  }
  img{
    border-radius: 20px;
  }
}
.cal{
  .box{
    height: 20rem;
    display: inline-block;
    img{
      filter: brightness(60%);
    }
    &:hover{
      h3, p{
        transform: translateY(-220px);
        transition: .9s;
      }
      img{
        filter: brightness(20%);
      }
      a{
        color: $gr;
      }
    }
  }
  .position-absolute{
    bottom: 5%;
    right: 4%;
    left: 4%;
    color: $w !important;
    font-size: 1.3rem;
    font-weight: 700;
    font-style: italic;
  }
}
.det{
  .carousel{
    height: 50rem !important;
    .h-100{
      
    }
  }
}
.cont{
  .heading{
    padding: 2rem 20rem !important;
  }
  #flexCheckChecked{
    width: 30px !important;
    height: 30px;
    background-color: $gr !important;
  }
  .text-success{
    color: #87C232 !important;
  }
}
.con1{
  background-color: $w;
}
.blog{
  h3{
    font-size: 1.5rem;
    font-style: italic;
    font-weight: 600 ;
    line-height: 2.5rem;
  }
  p{
    font-size: 1rem;
    font-style: italic;
    font-weight: 400 ;
    line-height: 2rem;
  }
  .btn-success{
    background-color: $gr !important;
    border: none;
    border-radius: 2px;
  }
  span{
    font-size: .9rem;
    font-style: italic;
    font-weight: 400 ;
    line-height: 2rem;
    color: $gr !important;
  }
  
}